import React from 'react';
import {View, StyleSheet,  Text, TouchableHighlight, ScrollView } from 'react-native';
import Services from '../Services';

const ListMedia = async ({value}) => {
    let medias = await Services.getMedia();

    onPress = (media) => {
        this.value = media.file;
    }
    return ( 
        <ScrollView style={styles.scrollView}>
            {medias.map((item,index)=>{
                return (
                    <TouchableHighlight  onPress={() => onPress(item)} key={`media-${index}`}>
                        <View style={styles.slideItem} key={`med-${index}`} >
                            <Text>{item.name}</Text>
                            <Image source={{ uri: media.file }} />
                        </View>
                    </TouchableHighlight>
                );
            })}
        </ScrollView>
    );
};

const styles = StyleSheet.create({
    scrollView:{
        height: 200
    },
    slideItem: {
        padding: 5,
        borderWidth:  1,
        margin: 5
    }
});

export default ListMedia;


        