import React from 'react';
import {View, StyleSheet,  Text, TouchableHighlight, ScrollView } from 'react-native';

const ListComponents = ({components, onPress}) => {
    return ( 
        <ScrollView style={styles.scrollView}>
            {components.map((item,index)=>{
                return (
                    <TouchableHighlight  onPress={() => onPress(item)} key={index}>
                        <View style={styles.slideItem} key={index} >
                            <Text>{item.name}</Text>
                        </View>
                    </TouchableHighlight>
                );
            })}
        </ScrollView>
    );
};

const styles = StyleSheet.create({
    scrollView:{
        height: 200
    },
    slideItem: {
        padding: 5,
        borderWidth:  1,
        margin: 5
    }
});

export default ListComponents;


        