import React, { useState, useEffect, useReducer, useCallback } from 'react';
import { ScrollView, View, KeyboardAvoidingView, StyleSheet, Text, ActivityIndicator, Alert, ImageBackground, Dimensions, TouchableOpacity} from 'react-native';
import Input from '../components/UI/Input';
import Card from '../components/UI/Card';
import { colors, appColors, typoSizes } from '../utils';
import bgInitialScreen from '../assets/img/intro-bg.png'
import LogoHolcim from '../assets/img/Holcim-logo-color.png'

const FORM_INPUT_UPDATE = 'FORM_INPUT_UPDATE';

import AppContext from "../context/AppContext";

const w = Dimensions.get('screen').width;
const h = Dimensions.get('screen').height;

const formReducer = (state, action) => {
  if (action.type === FORM_INPUT_UPDATE) {
    const updatedValues = {
      ...state.inputValues,
      [action.input]: action.value
    };
    const updatedValidities = {
      ...state.inputValidities,
      [action.input]: action.isValid
    };
    let updatedFormIsValid = true;
    for (const key in updatedValidities) {
      updatedFormIsValid = updatedFormIsValid && updatedValidities[key];
    }
    return {
      formIsValid: updatedFormIsValid,
      inputValidities: updatedValidities,
      inputValues: updatedValues
    };
  }
  return state;
};

const AuthScreen = props => {
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState();
  const [isSignup, setIsSignup] = useState(false);

  const [formState, dispatchFormState] = useReducer(formReducer, {
    inputValues: {
      email: '',
      password: ''
    },
    inputValidities: {
      email: false,
      password: false
    },
    formIsValid: false
  });

  useEffect(() => {
    if (error) {
      Alert.alert('An Error Occurred', error, [{ text: 'Okay' }]);
    }
  }, [error]);

  const dispatch = React.useContext(AppContext);

  const authHandler = async () => {
    setError(null);
    setIsLoading(true);

    dispatch.loginWithFetch(
      formState.inputValues.email,
      formState.inputValues.password
    )
    .then((fulfilledResult) => {
      console.log(fulfilledResult);
    })
    .catch((err) => {
      //console.log('err >>> ', err);
      setError(err.message);
      setIsLoading(false);
    });
  };

  const inputChangeHandler = useCallback(
    (inputIdentifier, inputValue, inputValidity) => {
      dispatchFormState({
        type: FORM_INPUT_UPDATE,
        value: inputValue,
        isValid: inputValidity,
        input: inputIdentifier
      });
    },
    [dispatchFormState]
  );

  return (
    <KeyboardAvoidingView
      behavior="padding"
      keyboardVerticalOffset={50}
      style={styles.screen}
    >
      <View style={styles.gradient}>
        <ImageBackground source={bgInitialScreen} style={styles.imageBG}/>

        <View style={styles.rightContainer}>
        
          <ImageBackground source={LogoHolcim} style={styles.logoHolcim} />
          <Card style={styles.authContainer}>
            <ScrollView>
              <Input
                id="email"
                label="Usuario"
                keyboardType="email-address"
                required
                email
                autoCapitalize="none"
                errorText="Please enter a valid email address."
                onInputChange={inputChangeHandler}
                initialValue=""
              />
              <Input
                id="password"
                label="Contrasea"
                keyboardType="default"
                secureTextEntry
                required
                minLength={5}
                autoCapitalize="none"
                errorText="Please enter a valid password."
                onInputChange={inputChangeHandler}
                initialValue=""
              />
              <View style={styles.buttonContainer}>
                {isLoading ? (
                  <ActivityIndicator size="small" color={colors[0]} />
                ) : (
                  <TouchableOpacity
                    style={styles.loginBtn}
                    onPress={authHandler}
                  ><Text style={styles.loginText}>{isSignup ? 'Sign Up' : 'Iniciar sesin'}</Text></TouchableOpacity>
                )}
              </View>
              
            </ScrollView>
          </Card>
        </View>
      </View>
    </KeyboardAvoidingView>
  );
};

export const screenOptions = {
  headerTitle: 'Authenticate'
};

const styles = StyleSheet.create({
  screen: {
    flex: 1
  },
  heading: {
    paddingBottom: 30,
    fontSize: 40,
    color: 'orangered'
  },
  rightContainer: {
    flex: 1,
    position: 'absolute',
    top: 0,
    left: 240,
    width: w - 240,
    height: h,
    justifyContent: 'center',
    alignItems: 'center',
  },
  authContainer: {
    width: '90%',
    maxWidth: 340,
    maxHeight: 400,
    padding: 20
  },
  buttonContainer: {
    marginTop: 20,
    backgroundColor: appColors[7],
    borderRadius: 10,
    color: '#fff',
    padding: 10

  },
  imageBG: {
    flex: 1,
    position: 'absolute',
    top: 0,
    left: -w +240,
    width: w,
    height: h
},
logoHolcim: {
  width: 148,
  height: 64,
  marginBottom: 50
},
loginBtn:{
  backgroundColor: appColors[7],
  justifyContent: 'center',
  alignItems: 'center',
  paddingVertical: 5
},
loginText:{
  color: appColors[0],
  fontSize: typoSizes[2]
}
});

export default AuthScreen;
