import React from 'react';
import { View, Text, TextInput, Button } from 'react-native';
import ListMedia from '../components/ListMedia';

const FormSlide = ({block, component, onSave, onCancel}) => {
    /*
    const [values, setValues] = React.useState(false);

    let params = (block.parameters!=null)? JSON.parse(block.parameters):null;
    let fields = JSON.parse(component.parameters);
    console.log("fields:",fields);
    for(let i=0; i < fields.length; i++)    {
        setValues([fields[i].name]) = (params[fields[i].name])? params[fields[i].name]:'';
    }*/

    let fields =[];
    let values=[];

    return ( 
        <View>
            {fields.map((item,index)=>{
                if(item.type=='text'){
                    return (
                        <View key={index} >
                        <Text>{item.label}</Text>
                        <TextInput value={ values[item.name] } />
                        </View>
                    )
                }
                if(item.type=='textarea'){
                    return (
                        <View key={index} >
                            <Text>{item.label}</Text>
                            <TextInput
                                multiline={true}
                                numberOfLines={4} value={ values[item.name] } />
                        </View>
                    )
                }
                if(item.type=='media'){
                    return (
                        <View key={index} >
                            <ListMedia value={ values[item.name] } />
                        </View>
                    )
                }
            })}
            <View>
                <Button
                    onPress={onSave(values)}
                    title="Guardar"
                    color="#841584"
                />
                <Button
                    onPress={onCancel}
                    title="Cancelar"
                    color="#ccc"
                />
            </View>
        </View>
    );
};

export default FormSlide;