import React from 'react';
import {
  StyleSheet,
  Text,
  View,
  Dimensions,
  TouchableOpacity,
  ImageBackground, Platform
} from 'react-native';
import { appColors, typoSizes } from '../../utils';
const w = Dimensions.get('screen').width;

const formatIindex = ( nbr ) => {
    if(nbr < 10) return "0"+nbr;
    return nbr;
}

const FooterControls = ({navigation, sideBarMode, sideBarOpen, indexSlide, goPrv, goNext, setOpenSlideMenu, setSideBarMode, duplicarSlide, eliminarSlide}) => {

    //console.log('FooterControls');
    return (
        <View style={[styles.footerControls, sideBarMode && sideBarOpen && styles.paddingArrows]}>
                                
            {sideBarMode ? 
                ( 
                <React.Fragment>
                    <View style={styles.arrows}>
                        <TouchableOpacity style={styles.prevItem} onPress={() => goPrv()}>
                            <ImageBackground style={styles.iconPrev} source={{uri: 'data:image/png;base64,'}} />
                        </TouchableOpacity>
                        <TouchableOpacity style={styles.nextItem} onPress={() => goNext()}>
                            <ImageBackground style={styles.iconNext} source={{uri: 'data:image/png;base64,'}} />
                        </TouchableOpacity>
                    </View>
                    <View style={styles.slideIndicator}>
                        <Text style={styles.whiteText}>Diapositiva {formatIindex(indexSlide+1)}</Text>
                    </View>
                    <TouchableOpacity style={styles.exit} onPress={() => navigation.goBack()}>
                        <Text style={styles.whiteText}>SALIR</Text>
                    </TouchableOpacity>
                </React.Fragment>
                ) : (
                    <React.Fragment>
                        <TouchableOpacity style={styles.newSlideBtn} onPress={() => setOpenSlideMenu(true)}>
                            <ImageBackground style={styles.iconSlide} source={{uri: 'data:image/png;base64,'}} />
                            <Text>Nuevo slide</Text>
                        </TouchableOpacity>

                        <View style={styles.inlineBlackBtns}>
                            <TouchableOpacity style={styles.blackBtnCenter} onPress={() => duplicarSlide()}>
                                <ImageBackground style={styles.iconDuplicar} source={{uri: 'data:image/png;base64,'}} />
                                <Text style={styles.greyText}>Duplicar</Text>
                            </TouchableOpacity>

                            <TouchableOpacity style={styles.blackBtnCenter} onPress={() => eliminarSlide()}>
                                <ImageBackground style={styles.iconEliminar} source={{uri: 'data:image/png;base64,'}} />
                                <Text style={styles.greyText}>Eliminar</Text>
                            </TouchableOpacity>
                        </View>
                    </React.Fragment>
                )
            }
            <View style={styles.inlineButtonsSlides}>
                <TouchableOpacity style={[styles.buttonListSlides, sideBarMode && styles.activeButton]} onPress={() => setSideBarMode(true)}>
                    <ImageBackground style={styles.iconListSlides} source={{uri: 'data:image/png;base64,'}} />
                </TouchableOpacity>
                <TouchableOpacity style={[styles.buttonGridSlides, !sideBarMode && styles.activeButton]} onPress={() => setSideBarMode(false)}>
                    <ImageBackground style={styles.iconGridSlides} source={{uri: 'data:image/png;base64,'}} />
                </TouchableOpacity>
            </View>
        </View>
    );
};

export default FooterControls;

