import React, { useReducer } from "react";
import axios from "axios";
import StorageApi from '../StorageApi';
import md5 from "react-native-md5";

import AxiosApi from '../AxiosApi';
import AppContext from "./AppContext";
import AppReducer from "./AppReducer";
import { AUTHENTICATE, SET_SEGMETS, SET_CURRENT_SEGMET, GET_NEWS, GET_DESCRIPTION } from "./types";

const GlobalState = (props) => {
  let initialState = {
    userData: null,
    didTryAutoLogin: false,
    segments: [],
    currentSegment: null
    //news: [],
    //activeNews: null,
    
  };

  const [state, dispatch] = useReducer(AppReducer, initialState);

  const getNews = async () => {
    try {
      let res = await axios.get("https://node-hnapi.herokuapp.com/news");
      let { data } = res;

      dispatch({ type: GET_NEWS, payload: data });
    } catch (error) {
      console.error(error);
    }
  };

  const getDescription = async (id) => {
    try {
      let res = await axios.get(`https://node-hnapi.herokuapp.com/item/${id}`);
      let { data } = res;

      dispatch({ type: GET_DESCRIPTION, payload: data });
    } catch (error) {
      console.error(error);
    }
  };

  const loginWithFetch = (email, password) => {

    return new Promise( async function (resolve, reject){

      const axiosApi = new AxiosApi();
      let storageApi = await StorageApi.getInstance(true);
      let response = await axiosApi.get("/ping");
      let isOnline = (response && response.status == 200);
      var token = null;
      var member = {};

      if(isOnline){
          let data={'email': email, 'password': password};
          let response = await axiosApi.post("/login",data);
          if(response && response.status == 200){
              token = response.data.access_token;
              member = response.data.member;
              //console.log('token > ', token);
              //console.log('response > ', response);
              resolve('successful login');
              dispatch({ type: AUTHENTICATE, payload:  {member: member, token: token } });
          }else{

              reject(new Error("No podemos autenticarlo (Online)!"));
          }
      }else{
          let data = await storageApi.get("/members/email/"+email);

          if (data.rows.length==0 || data.rows.item(0).password != md5.hex_md5(password)){

              reject(new Error("No podemos autenticarlo (Offline)!"));
              
          }else{
            resolve('successful login');
            dispatch({ type: AUTHENTICATE, payload:  {member: data.rows.item(0).member, token: 'tokenPlaceholder' } });
          }
      }
      
    });
  };

  const setSegments = (dataSegments) => {

    dispatch({ type: SET_SEGMETS, payload: dataSegments });
  };

  const setCurrentSegment = (idSegment) => {

    dispatch({ type: SET_CURRENT_SEGMET, payload: idSegment });
  };

  return (
    <AppContext.Provider
      value={{
        userData: state.userData,
        didTryAutoLogin: state.didTryAutoLogin,
        segments: state.segments,
        currentSegment: state.currentSegment,
        //news: state.news,
        //activeNews: state.activeNews,
        //getNews,
        //getDescription,
        loginWithFetch,
        setSegments,
        setCurrentSegment
      }}
    >
      {props.children}
    </AppContext.Provider>
  );
};

export default GlobalState;