import { AUTHENTICATE, SET_SEGMETS, SET_CURRENT_SEGMET, GET_NEWS, GET_DESCRIPTION } from "./types";

export default (state, action) => {
  const { payload, type } = action;

  switch (type) {
    case AUTHENTICATE:
        return {
          ...state,
          userData: payload,
          didTryAutoLogin: true
        };
    case SET_SEGMETS:
        return {
            ...state,
            segments: payload
        };
    case SET_CURRENT_SEGMET:
        return {
            ...state,
            currentSegment: payload
        };
    case GET_NEWS:
        return {
            ...state,
            news: payload
        };
    case GET_DESCRIPTION:
        return {
            ...state,
            activeNews: payload
        };
    default:
        return state;
  }
};