import React from 'react';
import {View, Dimensions,StyleSheet, Text, Image,Pressable, TouchableOpacity, ImageBackground} from 'react-native';
import { appColors, typoSizes } from '../utils';
import {useNavigation} from '@react-navigation/native';
const w = Dimensions.get('screen').width;

const ArticleItem = ({article, index, idShared}) => {
    const { title, description } = article;
    const navigate = useNavigation();
    return (
        <Pressable 
            style={[styles.cardItem, (index == 0) ? styles.cardItemDark : '']}
            onPress={() => navigate.navigate('SubFolder Screen', {item: article, idShared, theme: (index > 0) ? 'grey' : 'dark' })}
            >
            <View style={styles.maincardContent}>   
                <Image source={{uri: article.image}} style={styles.slideImage}></Image>
                <Text style={[styles.cardTextTitle, (index == 0) ? styles.whiteText : '']}>{ title }</Text>
                <Text style={[styles.cardTextParagraph, (index == 0) ? styles.whiteText : '']}>{description}</Text>
            </View>
            <View style={styles.bottomContent}>
                <Text style={[styles.greyBottomText, (index == 0) ? styles.whiteText : '']}>( 8 Materiales )</Text>
                <TouchableOpacity style={[styles.greyIcon, (index == 0) ? styles.whiteText : '']} onPress={() => navigate.navigate('WebViewScreen')}>
                    <ImageBackground style={styles.iconPrev} source={{uri: 'data:image/png;base64,'}} />
                </TouchableOpacity>
            </View>
        </Pressable>
    );
};

const ListSearchResults = ({results}) => {
    return ( 
        <View>
            {results.map((item,index) => {
                return (
                    <ArticleItem key={`pre-${index}`} article={item} index={index} />
                );
            })}
        </View>
    );
};

const styles = StyleSheet.create({
    scrollView:{
        height: 200
    },
    slideItem: {
        color: '#fff',
        padding: 5,
        borderWidth:  1,
        margin: 5
    },
    slideImage: {
        width: 150,
        height: 150
    },
    bottomContent: {
        position: 'absolute',
        bottom: 0,
        left: 0,
        width: '100%',
        flexDirection: 'row',
        justifyContent: 'space-between',
        alignItems: 'center',
        zIndex: 3,
        padding: 15
    },
    iconPrev: {
        width: 12,
        height: 12
    },
    greyIcon: {
        width: 40,
        height: 40,
        backgroundColor: appColors[6],
        borderRadius: 5,
        justifyContent: 'center',
        alignItems: 'center',
        transform: [{ rotate: '180deg'}]
    },
    greyBottomText: {
        color: appColors[6],
        fontSize: typoSizes[4],
    },
    imageBG: {
        width: '100%',
        height: '100%',
        left: 0,
        borderRadius: 10,
        overflow: "hidden",
        zIndex: 1
    },
    listTitle: {
        fontSize: typoSizes[1],
        fontWeight: "700",
        color: appColors[7],
        marginTop: 30,
        marginLeft: 20,
        alignSelf: 'flex-start'
    },
    cardTextTitle: {
        fontSize: typoSizes[2],
        fontWeight: "500",
        color: appColors[7]
    },
    cardTextParagraph: {
        fontSize: typoSizes[3],
        fontWeight: "400",
        color: appColors[6],
        marginTop: 6,
        lineHeight: 24
    },
    whiteText: {
        color: appColors[0],
        backgroundColor: 'transparent'
    },
    inlineCards: {
        width: w,
        flexWrap: 'wrap',
        flexDirection: 'row',
        justifyContent: 'space-between',
        paddingHorizontal: 20,
        paddingVertical: 30
    },
    cardItem: {
        width: w * .309,
        height: 190,
        borderRadius: 10,
        borderColor: appColors[5],
        borderWidth: 1,
        marginBottom: 20
    },
    cardItemDark: {
        backgroundColor: appColors[1]
    },
    maincardContent: {
        padding: 15,
    }
});

export default ListSearchResults;


        