import * as React from 'react';
import { View, StyleSheet, Pressable } from 'react-native';
import { appColors, typoSizes } from '../utils';
import Animated from 'react-native-reanimated';

const CustomTabBar = ({ state, descriptors, navigation, position }) => {
  return (
    <View style={[{width: state.routes.length * 230}, styles.myTabBar]}>
      {state.routes.map((route, index) => {
        const { options } = descriptors[route.key];
        const label =
          options.tabBarLabel !== undefined
            ? options.tabBarLabel
            : options.title !== undefined
            ? options.title
            : route.name;

        const isFocused = state.index === index;

        const onPress = () => {
          const event = navigation.emit({
            type: 'tabPress',
            target: route.key,
          });

          if (!isFocused && !event.defaultPrevented) {
            navigation.navigate(route.name);
          }
        };

        const onLongPress = () => {
          navigation.emit({
            type: 'tabLongPress',
            target: route.key,
          });
        };
        // modify inputRange for custom behavior
        const inputRange = state.routes.map((_, i) => i);
        const opacity = Animated.interpolate(position, {
          inputRange,
          outputRange: inputRange.map(i => (i === index ? 1 : 1)),
        });

        return (
            <View style={styles.tabItem} key={index}>
                <Pressable
                    accessibilityRole="button"
                    accessibilityState={isFocused ? { selected: true } : {}}
                    accessibilityLabel={options.tabBarAccessibilityLabel}
                    testID={options.tabBarTestID}
                    onPress={onPress}
                    onLongPress={onLongPress}
                    style={{ flex: 1, justifyContent: 'center', alignItems: 'center', backgroundColor: isFocused ? appColors[0] : appColors[7] }}
                >
                    <Animated.Text style={{ fontSize: typoSizes[2], fontWeight: '600', color: isFocused ? appColors[7] : appColors[6] }}>{label}</Animated.Text>
                </Pressable>
            </View>
        );
      })}
    </View>
  );
}

const styles = StyleSheet.create({
  myTabBar: {
      flexDirection: 'row', 
      marginLeft: 20, 
      backgroundColor: appColors[7],
      borderRadius: 10, 
      borderBottomLeftRadius: 0,
      borderBottomRightRadius: 0,
      overflow: 'hidden' 
  },
  tabItem: {
      width: 230, height: 80
  }
});

  export default CustomTabBar;