import React from 'react';
import {View, Dimensions, FlatList, Pressable, StyleSheet, Text, TouchableOpacity, ImageBackground} from 'react-native';
import { appColors, typoSizes } from '../utils';
import {useNavigation} from '@react-navigation/native';
const w = Dimensions.get('screen').width;
import {Herramientas} from '../models/data';

const ArticleItem = ({article, index}) => {
    const { description, title } = article;
    const navigate = useNavigation();

    const idShared = 1;

    const navigationSwitch = () => {

        console.log('linkTo > > ', title);

        if (title == 'Links externos') navigate.navigate('Links Screen')

        if (title == 'Fichas tcnicas') navigate.navigate('SubFolder Screen', {item: article, idShared, theme: (index > 0) ? 'grey' : 'dark', mode: title })

    }
    return (
        <Pressable 
            style={[styles.cardItem, (index == 0) ? styles.cardItemDark : '']}
            onPress={ () => navigationSwitch() }
            >
            <View style={styles.maincardContent}>   
                <Text style={[styles.cardTextTitle, (index == 0) ? styles.whiteText : '']}>{ title }</Text>
                <Text style={[styles.cardTextParagraph, (index == 0) ? styles.whiteText : '']}>{description}</Text>
            </View>
            <View style={styles.bottomContent}>
                <Text style={[styles.greyBottomText, (index == 0) ? styles.whiteText : '']}>( 8 Materiales )</Text>
                <TouchableOpacity style={[styles.greyIcon, (index == 0) ? styles.whiteText : '']} onPress={() => navigate.navigate('WebViewScreen')}>
                    <ImageBackground style={styles.iconPrev} source={{uri: 'data:image/png;base64,'}} />
                </TouchableOpacity>
            </View>
        </Pressable>
    );
};
const ListTools = () => {
  
  return (
    <View style={{ flex: 1, justifyContent: 'flex-start', alignItems: 'center', backgroundColor: appColors[0] }}>
        <Text style={styles.listTitle}>Holcim herremientas</Text>
        <View style={styles.cardGrid}>
            <View style={styles.inlineCards}>
            {Herramientas.map((item, index) => {
                return (
                    <ArticleItem key={`tool-${index}`} article={item} index={index} />
                )
            })}
            </View>
        </View>
    </View>
  );
};

