import * as React from 'react';
import { SafeAreaView, ImageBackground, Text, View, StyleSheet, Dimensions, TouchableOpacity, Image } from 'react-native';
import { appColors } from '../utils';
import {useNavigation} from '@react-navigation/native';
import bgInitialScreen from '../assets/img/intro-bg.png';
import LogoHolcim from '../assets/img/Holcim-logo-white.png';


const useScreenDimensions = () => {
    const [screenData, setScreenData] = React.useState(Dimensions.get('screen'));
  
    React.useEffect(() => {
        const onChange = (result) => {
            setScreenData(result.screen);
        };
  
        Dimensions.addEventListener('change', onChange);
        return () => Dimensions.removeEventListener('change', onChange);
    });
  
    return {
        ...screenData,
        isLandscape: screenData.width > screenData.height,
    };
};
  

const InitialScreen = () => {
    const navigate = useNavigation();
    const screenData = useScreenDimensions();

    return (
        <SafeAreaView style={styles.container}>
            <ImageBackground source={bgInitialScreen} 
                style={[{width: screenData.width, height: screenData.height}, styles.imageBG]}>
                <View style={[
                    { width: (screenData.isLandscape) ? (screenData.width * 0.45) : (screenData.width * 0.8),
                    height: (screenData.isLandscape) ? (screenData.height * 0.7) : (screenData.height * 0.6)},
                    styles.redArea
                    ]}>  
                    <Text style={styles.text}>Bienvenido a</Text>
                    <ImageBackground source={LogoHolcim} style={styles.logoHolcim} />
                    
                    <TouchableOpacity style={styles.cta} onPress={() => navigate.navigate('Auth Screen')}>
                        <Image style={styles.enterIcon}
                                source={{
                                uri:
                                    'data:image/png;base64,',
                                }}
                            />
                        <Text style={styles.textCta}> Iniciar Sesin</Text>
                    </TouchableOpacity>
                </View>
            </ImageBackground>
        </SafeAreaView>
    );
};

const styles = StyleSheet.create({
    container: {
        flex: 1,
    },
    imageBG: {
        flex: 1,
        position: 'absolute',
        top: 0,
        left: 0,
    },
    redArea: {
        backgroundColor: appColors[2],
        position: 'absolute',
        alignItems: 'flex-start',
        justifyContent: 'flex-start',
        bottom: 0,
        right: 0,
        padding: 60,
        paddingRight: 30
    },
    text: {
        position: 'relative',
        fontSize: 22, 
        fontWeight: "500",
        color: 'white',
        marginTop: 20,
        marginBottom: 40
    },
    logoHolcim: {
        width: 148,
        height: 64,
        marginBottom: 150
    },
    cta: {
        position: 'absolute',
        bottom: 60,
        right: 60,
        flexDirection: 'row',
        justifyContent: 'space-between',
        alignItems: 'center',
    },
    enterIcon: {
        width: 30,
        height: 25,
        marginRight: 10
    },
    textCta: {
        fontSize: 22, 
        fontWeight: "500",
        color: 'white',
    }
});

export default InitialScreen;