import * as React from 'react';
import { Dimensions, StatusBar, Easing } from 'react-native';
import { createStackNavigator } from '@react-navigation/stack';
import { ContactScreen } from '../screens/Screens';
import WebViewScreen from '../screens/WebViewScreen';
import AuthScreen from '../screens/AuthScreen';
import InitialScreen from '../screens/InitialScreen';
import LoaderScreen from '../screens/LoaderScreen';
import HomeScreen from '../screens/HomeScreen';
import SearchScreen from '../screens/SearchScreen';
import LinksScreen from '../screens/LinksScreen';
import AddBloqueScreen from '../screens/AddBloqueScreen';
import UserProfile from '../screens/UserProfile';
import ProfileSettingsScreen from '../screens/ProfileSettingsScreen';
import SegmentScreen from '../screens/SegmentScreen';
import SubFolderScreen from '../screens/SubFolderScreen';
import NewPresentationScreen from '../screens/NewPresentationScreen'
import PresentationScreen from '../screens/PresentationScreen';
import { createSharedElementStackNavigator } from 'react-navigation-shared-element';


const MainApp = createSharedElementStackNavigator();
const options = {
  gestureEnabled: false,
  headerBackTitleVisible: false,
  transitionSpec: {
    open: {
      animation: 'timing',
      config: { duration: 300, easing: Easing.inOut(Easing.ease) },
    },
    close: {
      animation: 'timing',
      config: { duration: 100, easing: Easing.inOut(Easing.ease) },
    },
  },
  cardStyleInterpolator: ({ current: { progress } }) => {
    return {
      cardStyle: {
        opacity: progress,
      },
    };
  },
};


const AuthStackNavigator = createStackNavigator();

export const AuthNavigator = () => {
  return (
    <AuthStackNavigator.Navigator initialRouteName={'Initial Screen'}>
      <AuthStackNavigator.Screen
        name="Initial Screen"
        component={InitialScreen}
        options={{headerShown: false}}
      />
      <AuthStackNavigator.Screen
        name="Auth Screen"
        component={AuthScreen}
        options={{headerShown: false}}
      />
    </AuthStackNavigator.Navigator>
  );
};

