import { appColors, typoSizes } from '../../utils';
const w = Dimensions.get('screen').width;

const HeaderControls = ({navigation, item, sideBarMode, htmlMsg, goPrv, crearNuevoSlideAfter, setOpenModalShare}) => {
  return (
        <View style={styles.headControls}>
            {!sideBarMode && <View style={styles.leftSideHead}>
                                <TouchableOpacity style={styles.greyIcon} onPress={() => navigation.goBack()}>
                                    <ImageBackground style={styles.iconPrev} source={{uri: 'data:image/png;base64,'}} />
                                </TouchableOpacity>
                                <Text style={styles.regresarText}>Salir</Text>
                            </View>
            }
            <View style={styles.rightSideHead}>
                <View style={styles.presName}>
                    
                    <Text style={styles.whiteTextName}>{item.title} {htmlMsg && '  ', htmlMsg}  </Text>
                </View>
                <View style={styles.separator}>
                </View>
                <TouchableOpacity style={styles.inlineHeaderIcon} onPress={() => crearNuevoSlideAfter()}>
                    <ImageBackground style={styles.iconHeader} source={{uri: 'data:image/png;base64,'}} />
                </TouchableOpacity>
                <TouchableOpacity style={styles.inlineHeaderIcon} onPress={() => setOpenModalShare(true)}>
                    <ImageBackground style={styles.iconHeaderShare} source={{uri: 'data:image/png;base64,'}} />
                </TouchableOpacity>
                <TouchableOpacity style={styles.inlineHeaderIcon} onPress={() => goPrv()}>
                    <ImageBackground style={styles.iconHeaderEye} source={{uri: 'data:image/png;base64,'}} />
                </TouchableOpacity>
            </View>
        </View>
    );
};

export default HeaderControls;

