const h = Dimensions.get('screen').height;
import * as FileSystem from 'expo-file-system';
import {useNavigation} from '@react-navigation/native';
import temporaryBg from '../../assets/img/texture.jpg';

const WIDTH_BREAKPOINT = 500;

const useScreenDimensions = () => {
  const [screenData, setScreenData] = React.useState(Dimensions.get('screen'));

  React.useEffect(() => {
      const onChange = (result) => {
          setScreenData(result.screen);
      };
      Dimensions.addEventListener('change', onChange);
      return () => Dimensions.removeEventListener('change', onChange);
  });
  return {
      ...screenData,
      isLandscape: screenData.width > screenData.height,
  };
};
const SegmentItem = ({item, onPress}) => {

  const pathImage = `${FileSystem.cacheDirectory}${item.image}`;

  const navigate = useNavigation();
  
  return (
    <Pressable onPress={() => onPress(item)} style={styles.card}>
      <View style={styles.topTextcard}>
        <Text style={styles.subTitle}>{item.subtitle}</Text>
        <Text style={styles.name}>{item.name} </Text>
      </View>
      {/* {Platform.OS == 'ios' && <View style={styles.view2} />} */}
      <View style={styles.view2} />
      <View style={styles.figure}>
        <SharedElement
            id={`item.${item.id}.image`}
            style={styles.image}
          >
          <Image source={{uri: pathImage}} style={styles.image} />
          
        </SharedElement>

        <View style={styles.buttons}>
          <TouchableOpacity style={styles.buttonRed} onPress={()=> navigate.navigate('New Presentation Screen', {item})}>
            <Text style={styles.buttonsTextWhite}>+ Crear presentacin</Text>
          </TouchableOpacity>
          <TouchableOpacity style={styles.buttonWhite} onPress={() => onPress(item)}>
            <Text style={styles.buttonsText}>Mis presentaciones</Text>
          </TouchableOpacity>
          <TouchableOpacity 
            style={[styles.buttonWhite, {borderColor: appColors[5], borderTopWidth: 1}]}
            onPress={() => onPress(item)}
            >
            <Text style={styles.buttonsText}>Herramientas</Text>
          </TouchableOpacity>
        </View>
      </View>
    </Pressable>
  );
};

