import React from 'react';
import { NavigationContainer } from '@react-navigation/native';
import { AuthNavigator, MainAppNavigation } from './Navigation';
import AppContext from "../context/AppContext";

const AppNavigator = props => {
  const state = React.useContext(AppContext);
  const is_Auth = state.userData;
  const is_didTryAutoLogin = state.didTryAutoLogin;

  return (
    <NavigationContainer>
      {is_Auth && <MainAppNavigation />}
      {!is_Auth && is_didTryAutoLogin && <MainAppNavigation />}
      {!is_Auth && !is_didTryAutoLogin && <AuthNavigator />}
    </NavigationContainer>
  );
};

export default AppNavigator;
