import React from 'react';
import {View, Dimensions, Animated} from 'react-native';
import SegmentItem from './UI/SegmentItem';
const w = Dimensions.get('screen').width;
const ListSegments = ({scrollX, slider, onPress, segmentos}) => {
  
  renderItem = ({item}) => <SegmentItem item={item} onPress={onPress} />;

  return (
    <View>
      <Animated.FlatList
        ref={slider}
        contentContainerStyle={{paddingHorizontal: 20}}
        horizontal
        pagingEnabled
        snapToInterval={w * 0.75}
        decelerationRate={0}
        bounces={false}
        showsHorizontalScrollIndicator={false}
        scrollEventThrottle={16}
        data={segmentos}
        keyExtractor={(item) => item.id+'-segment'}
        onScroll={Animated.event(
            [{ nativeEvent: { contentOffset: { x: scrollX } } }],
            { useNativeDriver: true }
        )}
        renderItem={renderItem}
      />
    </View>
  );
};

export default ListSegments;
