import React from 'react';
import {View, Dimensions, Pressable, StyleSheet, Text, TouchableOpacity, Image, Linking} from 'react-native';
import { appColors, typoSizes } from '../../utils';
const w = Dimensions.get('screen').width;

const IconLink = ({type}) => {

    if( type === 'imagen'){
        return(
            <Image style={styles.enterIconImg}
                source={{
                uri:
                    'data:image/png;base64,'
                }}
            />
        )
    }
    if( type === 'certificado'){
        return(
            <Image style={styles.enterIconTall}
                source={{
                uri:
                    'data:image/png;base64,'
                }}
            />
        )
    }
    return (
        <Image style={styles.enterIcon}
                source={{
                uri:
                    'data:image/png;base64,'
                }}
            />
    );

}


const LinkItem = ({link, onPress}) => {
    
    const formatDateLink = (dateToFormat) => {
        
        let dateStr = dateToFormat.split(' ');
        dateStr = dateStr[0].split('-');
        return "Actualizado el "+dateStr[2]+" / "+dateStr[1]+" / "+dateStr[0]+"  ";
    }

    
    return (
        <View style={[styles.linkElement, styles.linkGrey, link.type === 'certificado' && styles.linkBlack, link.type === 'salesforce' && styles.linkRed]}>
            <IconLink type={link.type}/>
            <Text style={styles.linkText}>{link.name}</Text>

            <View style={styles.rightIcons}>
                <Text style={styles.updatedText}>{link.updated_at && formatDateLink(link.updated_at)}</Text>

                <View style={styles.iconBG}>
                    <Image style={styles.heartIcon}
                        source={{ uri: 'data:image/png;base64,'}}
                    />

                </View>
                <TouchableOpacity style={styles.iconBG} onPress={() => Linking.openURL(link.url)}>
                    
                    <Image style={styles.playIcon} 
                        source={{uri: 'data:image/png;base64,'}} />

                </TouchableOpacity>
            </View>
        </View>
    );
};

const styles = StyleSheet.create({
    
    linkElement:{
        width: '100%',
        flexDirection: 'row',
        justifyContent: 'flex-start',
        alignItems: 'center',
        padding: 20,
        paddingVertical: 12,
        paddingHorizontal: 25,
        paddingRight: 15,
        borderRadius: 10,
        marginVertical: 7
    },
    linkGrey: {
        backgroundColor: appColors[7]
    },
    linkBlack: {
        backgroundColor: appColors[1]
    },
    linkRed: {
        backgroundColor: appColors[2]
    },
    rightIcons:{
        flexDirection: 'row',
        justifyContent: 'flex-end',
        alignItems: 'center',
        flex: 2,
    },
    iconBG:{
        width: 37,
        height: 37,
        borderRadius: 4,
        backgroundColor: 'rgba(0,0,0,.7)',
        justifyContent: 'center',
        alignItems: 'center',
        marginLeft: 15
    },
    heartIcon:{
        width: 17,
        height: 15,
    },
    playIcon:{
        width: 13,
        height: 13,
        transform: [{ rotate: '180deg'}]
    },
    
    enterIcon: {
        width: 26.5,
        height: 20,
        marginRight: 10
    },
    enterIconTall: {
        width: 20,
        height: 25,
        marginRight: 10
    },
    enterIconImg: {
        width: 25,
        height: 25,
        marginRight: 10
    },
    linkText: {
        color: appColors[0],
        fontSize: typoSizes[3],
        marginLeft: 15,
        fontWeight: "500"
    },
    updatedText: {
        color: appColors[0],
        fontSize: typoSizes[3],
        marginRight: 35,
        fontWeight: "500"
    },
    
    centerContent: { 
        position: 'absolute',
        width: w / 5,
        height: 270,
        left: '50%',
        marginLeft: - (w / 10),
        top: 60,
        justifyContent: 'flex-start',
        alignItems: 'center'
    },
    
    text: {
        fontSize: 22, 
        fontWeight: "500",
        color: 'white',
        marginTop: 20,
        marginBottom: 40
    },
    
    
});


export default LinkItem;