import React from 'react';
import {View, Dimensions, Pressable, StyleSheet, Text, TouchableOpacity, ImageBackground} from 'react-native';
import { appColors, typoSizes } from '../utils';
import {useNavigation} from '@react-navigation/native';
const w = Dimensions.get('screen').width;
import StorageApi from '../StorageApi';

const FichaItem = ({article, index, onPress}) => {
    const { title, description } = article;
    const navigate = useNavigation();
    console.log('article >> ', article);
    return (
        <Pressable 
            style={[styles.cardItem, '']}
            onPress={() => navigate.navigate('Presentation Screen')}
            >
            <View style={styles.maincardContent}>   
                <Text style={[styles.cardTextTitle, '']}>{ title }</Text>
                <Text style={[styles.cardTextParagraph, '']}>{description}</Text>
            </View>
            <View style={styles.bottomContent}>
                <Text style={[styles.greyBottomText, '']}>( 8 Materiales )</Text>
                <TouchableOpacity style={[styles.greyIcon, '']} onPress={() => navigate.navigate('WebViewScreen')}>
                    <ImageBackground style={styles.iconPrev} source={{uri: 'data:image/png;base64,'}} />
                </TouchableOpacity>
            </View>
        </Pressable>
    );
};

const ListFichas = () => {
    const [items, setItems] = React.useState([]);
    const [folder, setFolder] = React.useState(0);

    React.useEffect(() => { 

        const fetchItems = async () => {
            let storageApi = await StorageApi.getInstance();
            let itemsAux=[
                {
                    id: 0,
                    title:"Mi presentacion",
                    description: '',
                    type: 'page',
                    image:null
                }
            ];
    
            let item={};
            let result = await storageApi.get("/pages/type_id/4");
            console.log('result >> >> ', result);
            for(let i=0; i< result.rows.length;i++){
                item = result.rows.item(i);
                itemsAux.push({
                    id: item.id, 
                    title: item.title,
                    description: item.description,
                    type: "ficha",
                    image: item.image
                });
            }
            console.log("items:",itemsAux);
        
            setItems(itemsAux);
        }

        fetchItems();
        
     }, []);

  return (
    <View style={{ flex: 1, justifyContent: 'flex-start', alignItems: 'center', backgroundColor: appColors[0] }}>
        <View style={styles.cardGrid}>
            {items && <View style={styles.inlineCards}>
            {items && items.map((item, index) => {
                return (
                    <FichaItem key={`sub-${index}`} article={item} index={index} />
                )
            })}
            </View>}
        </View>
    </View>
  );
};

