    static async login(userCredentials){
        //Testing connectivity to syncronization service
        const storageApi = await StorageApi.getInstance();
        const axiosApi = new AxiosApi();
        let response = await axiosApi.get("/ping");
        if(response && response.status == 200){
            console.log("Service is online, sinchronizing");
            if(userCredentials.token == null){
                console.log("UPS, There is no token.")
            }else{
                await storageApi.sync(userCredentials.member, userCredentials.token);
            }
        }else{
            console.log("Service is offline, working with local data");
        }
    
        //Autenticate user
        await storageApi.showData("/members");

        let result = await storageApi.get("/members/email/"+userCredentials.member.email);
        if(result.rows.length==0){
            throw new Error('No podemos autenticarlo (No hay usuario)');
        }else{
            //Getting segments
            console.log('result.rows AA > ', result.rows.item(0).id);
            
            result = await storageApi.get("/segments/member_id/"+result.rows.item(0).id);

            console.log('result > ', result);
            if(result.rows.length==0){
                throw new Error('No podemos autenticarlo (No hay segmentos)');
            }else{
                return result.rows._array;
            }
        }
    }

