    /**
     * List of blocks (slides) from page (presentation)
     * @param {*} pageId
     */
    static async getBlocksFromPage(pageId){
        let storageApi = await StorageApi.getInstance();
        let query=`SELECT b.* FROM blocks as b LEFT JOIN block_page as bp ON (b.id = bp.block_id) WHERE bp.page_id=`+pageId+` ORDER BY bp.created_at ASC`;
        let result = await storageApi.query(query,[]);
        return result.rows._array;
    }

