import * as React from 'react';
import { SafeAreaView, ImageBackground, StyleSheet, Dimensions} from 'react-native';
import { appColors } from '../utils';
import {useNavigation} from '@react-navigation/native';
import Services from '../Services';
//import { GlobalDispatchContext } from '../store/GlobalContextProvider';
import AppContext from "../context/AppContext";

const useScreenDimensions = () => {
    const [screenData, setScreenData] = React.useState(Dimensions.get('screen'));
  
    React.useEffect(() => {
        const onChange = (result) => {
            setScreenData(result.screen);
        };
        Dimensions.addEventListener('change', onChange);
        return () => Dimensions.removeEventListener('change', onChange);
    });
    return {
        ...screenData,
        isLandscape: screenData.width > screenData.height,
    };
};

const LoaderScreen = () => {
    const navigate = useNavigation();
    const screenData = useScreenDimensions();
    const state = React.useContext(AppContext);
    const userCredentials = state.userData;
    const dispatch = React.useContext(AppContext);
    React.useEffect(() => {
        const onSyncFunction = async() => {
            let segments = await Services.login(userCredentials);
            dispatch.setSegments(segments);
            navigate.navigate('Home Screen');
        };
        onSyncFunction();
    }, []);

    
    return (
        <SafeAreaView style={styles.container}>
            <ImageBackground 
                style={[{width: screenData.width, height: screenData.height}, styles.imageBG]}>
                <ImageBackground source={{uri: 'data:image/png;base64,'}} style={styles.logoHolcim} />
            </ImageBackground>
        </SafeAreaView>
    );
};

const styles = StyleSheet.create({
    container: {
        flex: 1,
    },
    imageBG: {
        flex: 1,
        position: 'absolute',
        top: 0,
        left: 0,
        justifyContent: 'center',
        alignItems: 'center',
        backgroundColor: appColors[4]
    },
    logoHolcim: {
        width: 133,
        height: 134,
    },
});

export default LoaderScreen;
