import React from 'react';
import {View, Dimensions, Pressable, StyleSheet, Text, TouchableOpacity, ImageBackground} from 'react-native';
import { appColors, typoSizes } from '../utils';
import {useNavigation} from '@react-navigation/native';
const w = Dimensions.get('screen').width;
import Services from '../Services';
import AppContext from "../context/AppContext";

const ArticleItem = ({article, index, onPress}) => {
    const { title, description, image, type_id } = article;
    const navigate = useNavigation();
    return (
        <Pressable 
            style={[styles.cardItem, '']}
            onPress={() => navigate.navigate('Presentation Screen' , {item: article, idShared: index, theme:  'dark' })}>
            {   
                image ? (
                    <ImageBackground style={styles.maincardContentWithBG} source={{uri: image}} >
                        <Text style={[styles.cardTextTitle, '']}>{ title }</Text>
                        <Text style={[styles.cardTextParagraph, '']}>{type_id}</Text>

                    </ImageBackground>
                ): (
                    <View style={styles.maincardContent}>   
                        <Text style={[styles.cardTextTitle, '']}>{ title }</Text>
                        <Text style={[styles.cardTextParagraph, '']}>{type_id}</Text>
                    </View>
                )
            }
            <View style={styles.bottomContent}>
                <Text style={[styles.greyBottomText, '']}>( 8 Materiales )</Text>
                <TouchableOpacity style={[styles.greyIcon, '']} onPress={() => navigate.navigate('WebViewScreen')}>
                    <ImageBackground style={styles.iconPrev} source={{uri: 'data:image/png;base64,'}} />
                </TouchableOpacity>
            </View>
        </Pressable>
    );
};


const ListSubFolders = ({folderSegmento}) => {
    const [items, setItems] = React.useState([]);
    const state = React.useContext(AppContext);

    // folderSegmento
    const fetchFolders = async () => {
        let itemsAux = await Services.getFolderContent(folderSegmento);
        console.log("ItemsAuxSubFolder:",itemsAux);
        setItems(itemsAux);
    }

    // Mis Presentaciones
    const fetchItems = async () => {
        //console.log('state.currentSegment  >> ', state.currentSegment);
        let itemsAux = await Services.getPresentationsFromSegment(state.currentSegment);
        console.log("ItemsAuxMisPres:",itemsAux,state.currentSegment);
        setItems(itemsAux);
    }

    React.useEffect(() => {
        //console.log('folderSegmento  >> ', folderSegmento);
        folderSegmento === '1' ? fetchItems() : fetchFolders();
     }, []);

  return (
    <View style={{ flex: 1, justifyContent: 'flex-start', alignItems: 'center', backgroundColor: appColors[0] }}>
        <View style={styles.cardGrid}>
            {items && <View style={styles.inlineCards}>
            {items.map((item, index) => {
                return (
                    <ArticleItem key={`sub-${index}`} article={item} index={index} />
                )
            })}
            </View>}
        </View>
    </View>
  );
};

