import React from 'react';
import {View, StyleSheet, Image, Text, Pressable, ScrollView, Dimensions, ImageBackground } from 'react-native';
import { appColors, typoSizes } from '../utils';
const w = Dimensions.get('screen').width;
const h = Dimensions.get('screen').height;

const formatIindex = ( nbr ) => {
    if(nbr < 10) return "0"+nbr;
    return nbr;
}

const ListSlides = ({blocks, onPress, indexSlide, layout}) => {
    let position = 0;

    console.log('layout >>> ', layout);

    if(layout){
        return ( 
        
            <ScrollView style={styles.scrollView}>
                {blocks.map((item,index)=>{
                    position = formatIindex(index+1);
                    return (
                        <Pressable onPress={() => onPress(item, index)} key={index}>
                            <View style={[styles.slideItem, indexSlide === index ? styles.slideActive : '']} key={index}>
                                { item.image !== null ? 
                                    (
                                        <ImageBackground source={{uri: item.image}} style={styles.slideBg}>
                                            <Text style={[styles.greyText, indexSlide === index ? styles.slideActiveText : '']}>{position}</Text>
                                        </ImageBackground>
                                    ) : (
                                        <View style={styles.slideBg}>
                                            <Text style={[styles.greyText, indexSlide === index ? styles.slideActiveText : '']}>{position}</Text>
                                        </View>
                                    )
                                }
                            </View>
                        </Pressable>
                    );
                })}
            </ScrollView>
        );
    }

    return ( 
        
        <View style={styles.scrollViewRow}>
            {blocks.map((item,index)=>{
                position = formatIindex(index+1);
                return (
                    <Pressable style={[styles.slideItemRow, indexSlide === index ? styles.slideActive : '']} onPress={() => onPress(item, index)} key={index}>
                        { item.image !== null ? 
                            (
                                <React.Fragment>
                                    <ImageBackground source={{uri: item.image}} style={styles.slideBg} />
                                    <Text style={[styles.greyTextRow, indexSlide === index ? styles.slideActiveText : '']}>{position}</Text>
                                </React.Fragment>
                            ) : (
                                <React.Fragment>
                                    <Text style={[styles.greyTextRow, indexSlide === index ? styles.slideActiveText : '']}>{position}</Text>
                                </React.Fragment>
                            )
                            
                        }
                    </Pressable>
                );
            })}
        </View>
    );
    
};

const styles = StyleSheet.create({
    scrollView:{
        height: h,
        paddingVertical: 20
    },
    scrollViewRow:{
        flex: 1,
        flexWrap: 'wrap',
        flexDirection: 'row',
        justifyContent: 'flex-start',
        alignItems: 'center',
        padding: 10,
        paddingTop: 10
    },
    slideItemRow: {
        borderColor: appColors[6],
        borderWidth: 1,
        borderRadius: 5,
        padding: 2,
        height: 110,
        width: '18.5%',
        marginLeft: '1.25%',
        marginBottom: 40,
    },
    greyTextRow:{
        position: 'absolute',
        width: '100%',
        textAlign: 'center',
        color: appColors[6],
        fontSize: typoSizes[4],
        zIndex: 2,
        bottom: -28
    },
    slideItem: {
        margin: 5,
        borderColor: appColors[6],
        borderWidth: 1,
        borderRadius: 5,
        marginBottom: 10,
        marginLeft: '12%',
        padding: 2,
        height: 90,
        width: '84%',
        width: 150
    },

    slideActive:{
        borderColor: appColors[0],
        borderWidth: 2,
    },
    whiteText:{
        color: appColors[0]
    },
    greyText:{
        position: 'absolute',
        left: -26,
        top: 0,
        color: appColors[6],
        fontSize: typoSizes[5],
        zIndex: 2
    },
    slideActiveText:{
        color: appColors[0],
    },
    slideBg:{
        borderRadius: 5,
        flex: 1,
        resizeMode: "cover",
        justifyContent: "center",
        
    }
});

export default ListSlides;


        