import * as React from 'react';
import { createMaterialTopTabNavigator } from '@react-navigation/material-top-tabs';
import CustomTabBar from './CustomTabBar';
import ListPresentaciones from './ListPresentaciones';
import ListTools from './ListTools';

const TabsSegmento = ({idShared}) => {
    const Tab = createMaterialTopTabNavigator();
    //console.log('TabsSegmento > ', idShared);
    return (
        <Tab.Navigator tabBar={props => <CustomTabBar {...props} />}>
            <Tab.Screen name="Mis Presentaciones" component={ListPresentaciones} initialParams={{ itemId: idShared }} />
            <Tab.Screen name="Herramientas" component={ListTools} />
        </Tab.Navigator>
    );
};

export default TabsSegmento;