import * as React from 'react';
import { ScrollView, ImageBackground, Text, View, StyleSheet, Dimensions, TouchableOpacity, Image, TextInput } from 'react-native';
import { appColors, typoSizes } from '../utils';
const w = Dimensions.get('screen').width;
const h = Dimensions.get('screen').height;
import whiteLogo from '../assets/img/Holcim-logo-white.png';
import AppContext from "../context/AppContext";
import Services from '../Services';

const NewPresentationScreen = ({navigation, route}) => {
    const { item } = route.params;
    const state = React.useContext(AppContext);
    const userCredentials = state.userData;
    const [nombre, setNombre] = React.useState('');

    const createPresentation = async () => {

        let segmentoID = item.id;
        let memberID = userCredentials.member.id;
        let success = await Services.newPresentation(nombre, memberID, segmentoID);

        if(success){
            success = {'id': success, 'title': nombre}
            navigation.navigate('Presentation Screen' , {item: success, idShared: segmentoID, theme:  'dark' })
        }
    };

    return (
        <ScrollView style={styles.container}>
            <View style={styles.heroScreen}>
                <View style={styles.headerBar}>
                    <View style={styles.leftSideHead}>
                        <TouchableOpacity style={styles.redIcon} onPress={() => navigation.goBack()}>
                            <ImageBackground style={styles.iconPrev} source={{uri: 'data:image/png;base64,'}} />
                        </TouchableOpacity>
                        <Text style={[styles.regresarText, styles.blackText]}>Regresar</Text>
                    </View>
                </View>
                <View style={[styles.blackBg, styles.greyBg] }>
                    <View style={styles.leftContent}>
                        <Text style={[styles.subTitle, styles.blackText]}>Crear presentacin</Text>
                        <Text style={[styles.mainTitle, styles.blackText]}>{item.name}</Text>
                    </View>
                    <ImageBackground source={whiteLogo} style={styles.tynyLogo} />
                </View>
            </View>
            <View style={styles.tabsContainer}>
                <View style={styles.formContainer}>
                    <View style={styles.fieldWithIcon}>
                        <TextInput
                            style={styles.searchField}
                            color={appColors[7]}
                            placeholderTextColor={appColors[6]}
                            placeholder="Escribe el nombre de la presentacin"
                            onChangeText={ nombre => setNombre(nombre)}
                            defaultValue={ nombre }
                        />
                        <View style={styles.iconWrap}>
                            <Image style={styles.iconPlaceholder} source={{ uri: 'data:image/png;base64,'}} />
                        </View>
                    </View>
                    <TouchableOpacity style={styles.submitRed} onPress={() => createPresentation()}>
                        <Text style={styles.subtitle}>+ Crear presentacin</Text>
                    </TouchableOpacity>
                </View>
            </View>
        </ScrollView>
    );
};


