    static async getInstance(debug=false) {
        //console.log("instance: ",StorageApi.instance);
        if(StorageApi.instance==null) {
            StorageApi.instance = new StorageApi();
            StorageApi.instance.axiosApi = new AxiosApi();
            StorageApi.instance.db = SQLite.openDatabase('db.holcim25', "3.0");
            await StorageApi.instance.createDatabase(); //Create tables if does not exists
        }
        StorageApi.instance.debug =debug;
        return StorageApi.instance;
    }

    uuidv4() {
        return 'xxxxxxxx_xxxx_4xxx_yxxx_xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
            var r = Math.random() * 16 | 0, v = c == 'x' ? r : (r & 0x3 | 0x8);
            return v.toString(16);
        });
    }

    formatDate(d){
        var month = d.getMonth()+1;
        if(month<10) month="0"+month;
        var day = d.getDate();
        if(day<10) day="0"+day;
        var year = d.getFullYear();
        var hour = d.getHours();
        if(hour<10) hour="0"+hour;
        var mins = d.getMinutes();
        if(mins<10) mins="0"+mins;
        var secs = d.getSeconds();
        if(secs<10) secs="0"+mins;
        return year+"-"+month+"-"+day+" "+hour+":"+mins+":"+secs;
    }
   
