    async query(query,params){      
        if(this.debug) console.log("Debug query: ",query);
        let promise = new Promise((resolve, reject) => {
            this.db.transaction((tx) => {
                tx.executeSql(query, params, (tx, results)=>{
                    resolve(results);
                },(t, error)=>{
                    console.log(query);
                    console.log("Query error: ",error);
                });
            });
        });
        return await promise;
    }

    async get(url){
        let matchResult= null;
        let query="";

        //Select Pages on folder
        matchResult= url.match(/\/pages\/folder\/(\d+(\.\d)*)/i)
        if(query=='' && matchResult != null ){
            if(matchResult[1]==0){
                query= `SELECT p.* 
                FROM pages as p LEFT JOIN page_folder as pf ON(pf.page_id = p.id) 
                WHERE pf.folder_id IS NULL`;
            }else{
                query= `SELECT p.* 
                    FROM pages as p LEFT JOIN page_folder as pf ON(pf.page_id = p.id) 
                    WHERE pf.folder_id=`+matchResult[1];
            }
            return await this.query(query,[]);
        }

        //Select Subfolders on folder
        matchResult= url.match(/\/folders\/parent\/(\d+(\.\d)*)/i)
        if(query=='' && matchResult != null ){
            query= 'SELECT * FROM folders WHERE folder_id='+matchResult[1];
            return await this.query(query,[]);
        }        

        //Select Member_page record
        matchResult= url.match(/\/member_page\/(\d+(\.\d)*)\/(\d+(\.\d)*)/i)
        if(query=='' && matchResult != null ){
            query= 'SELECT * FROM member_page WHERE member_id='+matchResult[1]+' AND page_id='+matchResult[3];
            return await this.query(query,[]);
        }

        //Select Member's segments
        matchResult= url.match(/\/segments\/member_id\/(\d+(\.\d)*)/i)
        if(query=='' && matchResult != null ){
            query= 'SELECT s.* FROM segments as s LEFT JOIN member_segment as ms ON(s.id=ms.segment_id) WHERE ms.member_id='+matchResult[1];
            return await this.query(query,[]);
        }

        //Select Member's pages
        matchResult= url.match(/\/pages\/member_id\/(\d+(\.\d)*)/i)
        if(query=='' && matchResult != null ){
            query= 'SELECT * FROM pages WHERE member_id='+matchResult[1];
            return await this.query(query,[]);
        }
        
        //Select Page's blocks
        matchResult= url.match(/\/blocks\/page_id\/(\d+(\.\d)*)/i)
        if(query=='' && matchResult != null ){            
            query=`SELECT b.* FROM blocks as b LEFT JOIN block_page as bp ON (b.id = bp.block_id) WHERE bp.page_id=`+matchResult[1]+` ORDER BY bp.created_at ASC`;
            return await this.query(query,[]);
        }

        //DELETE by FIELD
        matchResult= url.match(/\/(\w+(\.\w)*)\/delete\/(\w+(\.\w)*)\/(\d+(\.\d)*)/i)
        if(query=='' && matchResult != null ){
            query='DELETE FROM '+matchResult[1]+" WHERE `"+matchResult[3]+"`="+matchResult[5];
            return await this.query(query,[]);
        }
        
        //DELETE ALL
        matchResult= url.match(/\/(\w+(\.\w)*)\/delete\/all/i)
        if(query=='' && matchResult != null ){
            query='DELETE FROM '+matchResult[1];
            return await this.query(query,[]);
        }

        //Select Get by FIELD
        matchResult= url.match(/\/(\w+(\.\w)*)\/(\w+(\.\w)*)\/(\w*@*[a-zA-Z-\u00f1\u00d1\.]*)/i);
        if(query=='' && matchResult != null ){
            query='SELECT * FROM '+matchResult[1]+" WHERE `"+matchResult[3]+"`='"+matchResult[5]+"'";
            return await this.query(query,[]);
        }

        //Select by ID
        matchResult= url.match(/\/(\w+(\.\w)*)\/(\d+(\.\d)*)/i);
        if(query=='' && matchResult != null ){
            query= 'SELECT * FROM '+matchResult[1]+" WHERE id="+matchResult[3];
            return await this.query(query,[]);
        }

        //Select ALL
        matchResult= url.match(/\/(\w+(\.\w)*)/i)
        if(query=='' && matchResult != null ){
            query = 'SELECT * FROM '+matchResult[1];
            return await this.query(query,[]);
        }
        
        return null;
    }

    async post(url,data){
        let matchResult= url.match(/\/(\w+(\.\w)*)\/(\d+(\.\d)*)/i);
        let queryResult=null;
        let tableName = matchResult[1];
        let tableId = matchResult[3];
        if(tableId >0){
            //UPDATE
            let setParams = "";
            let separator ="";
            let keys = Object.keys(data);
            let key=null;
            let value = null;
            for(let i=0; i < keys.length; i++){
                key = keys[i];
                value = data[keys[i]];
                if(typeof value === 'string'){                    
                    setParams += separator+"`"+ key +"`='"+ value+"'";
                }else{
                    setParams += separator+"`"+ key +"`="+ value;
                }
                separator=",";
            }
            queryResult = await this.query("UPDATE `"+tableName+"` SET "+ setParams +" WHERE id="+tableId,[]);
        }else{
            //INSERT ID
            let insertFields = "";
            let insertParams = "";
            let separator ="";
            let keys = Object.keys(data);
            let key=null;
            let value = null;
            for(let i=0; i < keys.length; i++){
                key = keys[i];
                insertFields += separator +"`"+ key+"`";
                value = data[keys[i]];
                if(typeof value === 'string'){                    
                    insertParams += separator + "'"+ value+"'";
                }else{
                    insertParams += separator + value;
                }
                separator=",";
            }
            queryResult = await this.query("INSERT INTO `"+tableName+"` ("+ insertFields +")VALUES("+ insertParams +")", []);
        }
        return queryResult;
    }

    toRow(data){
        let returns ="";
        let keys = Object.keys(data);
        let value = null;
        let key=null;
        let separator="";
        for(let i=0; i < keys.length; i++){
            key = keys[i];
            value = data[keys[i]];
            if(typeof value === 'string'){             
                if(value.length>512) value = value.substring(0, 20)+"...";
                returns += separator+"`"+ key +"`='"+ value+"'";
            }else{
                returns += separator+"`"+ key +"`="+ value;
            }
            separator=" | ";
        }
        return returns;
    }
      
