    urlToBlob(url) {
        return new Promise((resolve, reject) => {
            var xhr = new XMLHttpRequest();
            xhr.onerror = reject;
            xhr.onreadystatechange = () => {
                if (xhr.readyState === 4) {
                    resolve(xhr.response.blob());
                }
            };
            xhr.open('GET', url);
            xhr.responseType = 'blob'; // convert type
            xhr.send();
        })
    }

    async syncLocalData(member,token){
        try {
            let remoteObj = null;
            let localObj =null;
            let memberLocal =null;
            let data = null;
            let response =null;
            let self=this;
            const config = {
                'headers': {'Authorization':'Bearer '+token}
            };
            //Saving local data to server
            //Saving Pages
            response = await self.get('/pages/member_id/'+ member.id);
            for(var i=0; i< response.rows.length; i++){
                let localPage = response.rows.item(i);

                //collecting blocks from page
                let response2 = await self.get('/blocks/page_id/'+localPage.id);
                let blocks = [];
                for(let j=0; j< response2.rows.length; j++){ 
                    localObj = response2.rows.item(j);
                    blocks.push(localObj);
                }
                
                data={
                    "page": localPage,
                    "blocks": blocks
                }
                //Sending data to server
                await this.axiosApi.post('/sync/pages', data,config);
            }

            //Saving Favs
            response = await self.get('/favourites/member_id/'+member.id);
            await this.axiosApi.post('/sync/setFavs', { "favourites": response.rows._array },config);

            return null;
        } catch (error) {
            console.log("Error",error);
        }
    }

