import * as React from 'react';
import { SafeAreaView, Text, View, StyleSheet, Dimensions, TouchableOpacity, TextInput, Image } from 'react-native';
import { AntDesign } from '@expo/vector-icons';
import { appColors, typoSizes } from '../utils';
import Services from '../Services';
import AppContext from "../context/AppContext";

const w = Dimensions.get('screen').width;
const h = Dimensions.get('screen').height;

const ModalShare = ({openModalShare, setOpenModalShare, slugShare}) => {
    const [nombre, setNombre] = React.useState('');
    const [email, setEmail] = React.useState('');
    const [confirmEmail, setConfirmEmail] = React.useState('');
    const state = React.useContext(AppContext);
    const userCredentials = state.userData;
    
    const submit = async () => {
        if(email == confirmEmail){
           let success = await Services.sendPdfInvite(slugShare,nombre,email,userCredentials)
        }else{
            alert("Emails no coinciden");
        }
    }
    
    return (
        <SafeAreaView style={[styles.container, openModalShare && styles.activeModal]}>
            <View style={styles.formContainer}>
                <View style={styles.lightGreyBG}></View>
                <AntDesign
                    name="close"
                    style={styles.close}
                    size={24}
                    color={'#FFF'}
                    onPress={() => setOpenModalShare(false)}
                />
                <View style={styles.formInner}>
                    <Text style={styles.subtitle}>Enviar presentacin</Text>
                    <View style={styles.inlineFieldset}>
                        <View style={styles.fieldWithIcon}>
                            <TextInput
                                style={styles.searchField}
                                color={appColors[1]}
                                placeholderTextColor={appColors[6]}
                                placeholder="Nombre"
                                onChangeText={ nombre => setNombre(nombre)}
                                defaultValue={nombre}
                            />
                            <View style={styles.iconWrap}>
                                <Image style={styles.iconPlaceholder} source={{ uri: 'data:image/png;base64,'}} />
                            </View>
                        </View>
                        <View style={styles.fieldWithIcon}>
                            <TextInput
                                style={styles.searchField}
                                color={appColors[1]}
                                placeholderTextColor={appColors[6]}
                                placeholder="Correo electrnico"
                                onChangeText={ email => setEmail(email)}
                                defaultValue={email}
                            />
                            <View style={styles.iconWrap}>
                                <Image style={styles.iconPlaceholderMsg} source={{ uri: 'data:image/png;base64,'}} />
                            </View>
                        </View>
                        <View style={styles.fieldWithIcon}>
                            <TextInput
                                style={styles.searchField}
                                color={appColors[1]}
                                placeholderTextColor={appColors[6]}
                                placeholder="Confirmar correo"
                                onChangeText={ confirmEmail => setConfirmEmail(confirmEmail)}
                                defaultValue={confirmEmail}
                            />
                            <View style={styles.iconWrap}>
                                <Image style={styles.iconPlaceholderMsg} source={{ uri: 'data:image/png;base64,'}} />
                            </View>
                        </View>
                    </View>
                    <TouchableOpacity style={styles.submitRed} onPress={() => submit()}>
                        <Text style={styles.subtitle}>Enviar</Text>
                    </TouchableOpacity>
                </View>
                
            </View>
        </SafeAreaView>
    );
};

export default ModalShare;

