import * as axios from 'axios';
class AxiosApi {

    //static HOST="http://192.168.3.2:8000";
    static HOST="https://holcimapp-dev.fooprojects.com";
    static API_URL= AxiosApi.HOST+"/api/app";
    
    get(url,config=null){
        console.log("---> GET",AxiosApi.API_URL+url);
        return axios.get(AxiosApi.API_URL+url,config).then((result)=>{
            return result;
        }).catch((error)=>{
            console.log(AxiosApi.API_URL+url);
            console.log(error.message);
        });
    }

    post(url,data,config=null){
        console.log("---> POST",AxiosApi.API_URL+url);
        return axios.post(AxiosApi.API_URL+url,data,config).then((result)=>{
            return result;
        }).catch((error)=>{
            console.log(AxiosApi.API_URL+url);
            console.log(data);
            console.log(error.message);
        });
    }

}

export default AxiosApi
