//Script to inject the slide
//setTimeout(function (){window.ReactNativeWebView.postMessage("Hello from the html!")}, 884000);

export default class WebViewScreen extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      blocksList: null,
      componentsList: null,
      webViewHtml: slideHtml({'component':'','parameters':'{}'}),
      webViewKey:0,
      selectedBlock: null,
      selectedComponent: null,
      showComponents: false
    };
  }

  componentDidMount = async () => {
    //Getting slides from blocks
    let self = this;
    let results = await Services.getPages();
    if(results.length>0){
      result = await storageApi.get("/blocks/page/"+results[0]);
      let blocks = result.rows._array;
      result = await storageApi.get("/components");
      let components = result.rows._array;
      //result = await storageApi.get("/media");
      //let medias = result.rows._array;
      self.setState({blocksList: blocks, componentsList: components});
    }
  }

  onSetSlide = (block) => {
    StorageApi.getInstance().then( async(storageApi) =>{
      storageApi.get("/components/name/"+block.component).then(data=> {
        this.setState({webViewHtml: slideHtml(block), selectedBlock: block, selectedComponent: data.rows.item(0), webViewKey: this.state.webViewKey++});
      });
    });
  }
  
  onSetComponent = (component) => {
    let block = null;
    this.setState({selectedBlock: {}, selectedComponent: component, showComponents: false});    
  }

  onSaveForm = () => {
    let self = this;
    StorageApi.getInstance().then( async(storageApi) =>{
      storageApi.post("/blocks/"+self.state.selectedBlock.id,self.state.selectedBlock).then(data=> {
        alert("saved");
        this.setState({selectedBlock: null});
      });
    });
  }

  onCancelForm = () => {
    this.setState({selectedBlock: null});
  }

  onNewForm = () => {
    this.setState({showComponents: true});
  }

  render() {
    return (
      <View style={styles.container}>
        <View>
          <Text style={styles.bold}>Lista diapositivas</Text>
          {this.state.blocksList && <ListSlides blocks={this.state.blocksList} onPress={this.onSetSlide} /> }
          <View>
            <Text style={styles.bold}>Lista de tipos</Text>
            {this.state.showComponents && this.state.componentsList && <ListComponents components={this.state.componentsList} onPress={this.onSetComponent} /> }
          </View>
          <Button
              style={styles.button}
              onPress={this.onNewForm}
              title="Nueva diapositiva"
              color="#841584"
          />
        </View>
        <WebView
            style={styles.webview}
            ref={(r) => (this.webref = r)}
            onMessage={event => {
              alert(event.nativeEvent.data);
            }}
            source= {{ html: this.state.webViewHtml }}/>
        {this.state.selectedBlock && 
          <View>
            <Text style={styles.bold}>Formulario diapositiva</Text>
            <FormSlide block={this.state.selectedBlock} component={this.state.selectedComponent } onSave={this.onSaveForm}  onCancel={this.onCancelForm} />
          </View>
        }
        
      </View>
      
    );
  }
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#fff',
    paddingVertical: 10
  },
  webview: {
    flex: 1,
    width: w,
    height: h / 2,
    marginTop: 10,
    borderWidth: 2,
    borderColor: appColors[2],
  },
  bold: {
    fontWeight: 'bold'
  },
  button:{
    borderWidth: 2,
    borderColor: appColors[2],
    width: 100,
    height: 60
  }
});

